import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import MoreMovies from "./components/AddMovies";
import MainComponent from "./components/mainComponent";
function App() {
  return (
    <Router>
      <Switch>
        <Route exact path="/">
          <MainComponent />
        </Route>
        <Route exact path="/addmovies">
          <MoreMovies />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
