import {Link} from 'react-router-dom'
import './header.css'
import { useState } from 'react'
// import {useNavigate} from 'react-router-dom'
// import {useHistory} from 'react-router-dom'
import validator from 'validator'
const MoreMovies = () => {
    // const navigate = useNavigate();
    // const history = useHistory();
    const [url1,setUrl] = useState("");
    const [errorMessage, setErrorMessage] = useState('')
    function handleChange(e ) {
        setUrl(e.target.value)
        if (validator.isURL(url1)) {

            setErrorMessage('Is Valid URL')
          } else {
            setErrorMessage('Is Not Valid URL')
          }
        }
  
    var color1 = `${errorMessage === "Is Valid URL" ? 'green' : 'red'}`;
    return (
        <>
        <center>
        <Link to="/"> <button className="dif_btn" style={{marginTop:"0.5rem"}}>Home</button>
</Link>
        <div style={{marginTop:"10vh",backgroundColor:"wheat",width:"400px"}}>
            <form method='post' action='https://brave-garment-crow.cyclic.app/addmovies' style={{display:"flex",justifyContent:"center",gap:"10px",width:"100%",height:"60vh",alignItems:"center",flexDirection:"column"}}>
                <input type="text" required name="title" placeholder="Enter the movie name" style={{padding:"0.6rem",borderRadius:"9px",width:"20rem"}}/>
                <br/>
                <input type="text" value={url1} required onChange={handleChange} name="poster_path" placeholder="Enter the movie image URL" style={{padding:"0.6rem",borderRadius:"9px",width:"20rem"}}/>
                <span style={{
          fontWeight: 'bold',
          color: color1,
        }}>{errorMessage}</span>
        <span style={{
            margin:"0.3rem",
            fontSize:"13px",
          fontWeight: 'light',
          color:'black',
        }}>In case, the URL is correct and site is displaying "Is Not Valid URL" , try removing the last character of the image URL and reinsert it. </span>
<br/>

<input type="submit" value="Submit" style={{cursor:"pointer",padding:"0.6rem",borderRadius:"9px",width:"20rem"}}/>
            </form>
        </div>
        </center>
        </>
    )
    }
export default MoreMovies