import React from "react";
import "./main.css";

const Main = (props) => {
  const data = props.data;
  console.log("Data is: ", data);
  return (
    <>
      <div className="container">
        <h2>{data.title}</h2>
        <img src={data.poster_path} className="img" />
      </div>
    </>
  );
};
export default Main;
