import Header from "./header"
import Main from "./main"
import { useState , useEffect } from "react";
const MainComponent = () => {
    useEffect(() => {
        fetch("https://brave-garment-crow.cyclic.app/")
        // fetch("http://localhost:9002/")
          .then((res) => res.json())
          .then((data) => {
            setMainArray(data);
          });
      }, []);
      const [mainArray, setMainArray] = useState();
    return (
        <>
              <Header />
              <div className="main">
                {mainArray &&
                  mainArray
                    .slice(0)
                    .reverse()
                    .map((item) => <Main data={item} />)}
              </div>
            </>
    )
}
export default MainComponent;